<template>

  <li class="result-craft">

    <div class="result-info" :class="cr.job !== 0 ? 'true-craft':''">
        <div class="result-name">
          <img :src="'img/items/' + cr.img + '.png'">
          <span>{{ cr.name }} x{{ cr.qt }}
            <template v-if="cr.job !== 0"><br> 
            {{ crafts.jobs.find( x => x.id === cr.job ).name}} (Nv.{{cr.lvl}})
            </template>
          </span>
        </div>

      <template v-if="cr.job !== 0">
      <div class="result-add" v-if="cr.ingredients.length > 1">
        <i class="fas fa-swatchbook" @click.prevent="upShow(cr)"></i>
      </div>
      </template>

    </div>

    <template v-if="cr.job !== 0">
      <ul>
        <formcraft :cr='c' v-for="c in cr.ingredients[cr.show]" :key='c.id'></formcraft>
      </ul>
    </template>
    <!-- {{cr.name}} x{{cr.qt}}
    <template v-if="cr.job !== 0">
        <a v-if="cr.ingredients.length > 1" href="" @click.prevent="upShow(cr)">Switch</a>
      <ul>
        <formcraft :cr='c' v-for="c in cr.ingredients[cr.show]" :key='c.id'></formcraft>
      </ul>
    </template> -->
  </li>

</template>

<script>
  import crafts from './Store'
  import formcraft from './Form'

  export default {
    name:'formcraft',

    components: { formcraft },

    data() {
      return {
        crafts: crafts.state
      }
    },

    props: ['cr'],

    methods: {
      upShow(cr) {
        if (cr.show + 1 === cr.ingredients.length) cr.show = 0
        else cr.show++
      }
    }

  }
</script>

<style>

</style>


