import Ingredient from './Ingredient.js'

class Store {
  constructor () {
    this.state = {
      list: [],
      jobs: [
        { id: 71, name: 'Forestier' },
        { id: 72, name: 'Herboriste' },
        { id: 73, name: 'Mineur' },
        { id: 64, name: 'Paysan' },
        { id: 75, name: 'Pêcheur' },
        { id: 74, name: 'Trappeur' },
        { id: 77, name: 'Armurier' },
        { id: 78, name: 'Bijoutier' },
        { id: 40, name: 'Boulanger' },
        { id: 76, name: 'Cuisinier' },
        { id: 81, name: 'Ebéniste' },
        { id: 83, name: 'Maitre d\'Armes' },
        { id: 80, name: 'Maroquinier' },
        { id: 79, name: 'Tailleur' },
      ],
      rarity: [
        { id: 1, name: 'Inhabituel' },
        { id: 2, name: 'Rare' },
        { id: 3, name: 'Mythique' },
        { id: 4, name: 'Légendaire' },
        { id: 5, name: 'Relique' },
        { id: 6, name: 'Souvenir' },
        { id: 7, name: 'Epique' }
      ]
    }
  }

  getIngs(craft, arr) {
    craft.ingredients[craft.show].forEach((ing) => {
      if (ing.job === 0) {
        let checkIng = arr.find( x => x.id === ing.id)
        if (checkIng === undefined) arr.push(new Ingredient(ing, ing.qt))
        else checkIng.qt += ing.qt
      } else {
        this.getIngs(ing, arr)
      }
    })
  }
}

export default new Store()


