<template>
  <section id="search">

    <div class="section-head">
      <h2>Recherche</h2>
    </div>

    <div class="section-body">

      <input placeholder="On se craft quoi aujourd'hui ?" id='input' type="text" v-model="search">

      <div class="no-result" v-if="getResults.length === 0">Aucun craft trouvé...</div>
      <div class="result-craft" :key="c.id" v-if="!crafts.list.find(x => x.id === c.id)" v-for="c in getResults" @click.prevent="addCraft(c)">

        <div class="result-info">
          <img :src="'img/items/' + c.img + '.png'">
          <div class="result-name">
            {{ c.name }}<br>
            <span>Niv. {{ c.lvl }} - {{ crafts.jobs.find( x => x.id === c.job ).name}}</span>
          </div>
        </div>
      </div>
    </div>


  </section>
</template>

<script>

  import crafts from './Store'
  import Craft from './Craft'

  export default {

    data() {
      return {
        crafts: crafts.state,
        search:'',
      }
    },

    computed: {
      getResults() {
        let result = []
        if (this.search.length > 0) {
          result = data.filter(x => x.job !== 0 && x.name.toLowerCase().indexOf(this.search.toLowerCase()) !== -1)
        }
        return result.sort(function (a, b) { return a.lvl - b.lvl })
      }
    },

    methods: {
      addCraft(craft) {
        this.crafts.list.push(new Craft(craft, 1))
      },
    }

  }

</script>

<style>
</style>


