<template>
  <section id="quantity">

    <div class="section-head">
      <h2>Stock</h2>
    </div>

    <div class="section-body">

      <div class="no-result" v-if="crafts.list.length === 0">Aucun craft dans la liste...</div>
      <div class="results" v-else>
        <span>{{ crafts.list.length }} {{crafts.list.length > 1 ? 'crafts': 'craft'}} en cours : </span>
        <i class="fas fa-undo" @click.prevent="removeAll()"></i>

      </div>

      <div class="result-crafts" v-for="c in crafts.list" :key="c.id">
        <div class="result-craft">
        
          <div class="result-info">
            <div class="remove-craft">
              <i class="far fa-times-circle" @click.prevent="removeCraft(c.id)"></i>
            </div>

            <img :src="'img/items/' + c.img + '.png'">
            <div class="result-name">
              {{ c.name }}<br>
              <span>Niv. {{ c.lvl }} - {{ crafts.jobs.find( x => x.id === c.job ).name}}</span>
            </div>
          </div>

          <div class="result-right" @click.self="upShow(c)">
            <div class="result-add">
              <i class="fas fa-swatchbook" v-if="c.ingredients.length > 1" @click.prevent="upShow(c)"></i>
            <input type="number" value="1" min="1" max="500" @change="changeQt(c, $event)">
              <i class="fas fa-chevron-circle-down" @click.prevent="setVisibility(c.id)" :class="visibleCraft.includes(c.id) ? 'turn-i':'turn-i-act'"></i>
            </div>
          </div>
        </div>

        <ul class="decomposition" v-if="visibleCraft.includes(c.id)">
            <formcraft :cr="c" v-for="c in c.ingredients[c.show]" :key="c.id"></formcraft>
        </ul>
      </div>
    </div>

  </section>
</template>

<script>

  import crafts from './Store'
  import formcraft from './Form'

  export default {
    data() {
      return {
        crafts: crafts.state,
        visibleCraft: []
      }
    },

    components: { formcraft },

    methods: {
      removeCraft(id) {
        this.crafts.list = this.crafts.list.filter( x => x.id !== id)
        this.visibleCraft = this.visibleCraft.filter( x => x !== id)
      },
      removeAll() {
        this.crafts.list = []
        this.visibleCraft = []
      },
      changeQt(c, $event) {
        if (+$event.target.value < 1 && +$event.target.value > 500) $event.target.value = 1
        let item = this.crafts.list.find( x => x.id === c.id).changeQt($event.target.value)
      },
      setVisibility(id) {
        if (this.visibleCraft.includes(id)) {
          this.visibleCraft = this.visibleCraft.filter( x => x !== x)
        } else {
          this.visibleCraft.push(id)
        }
      },
      upShow(cr) {
        if (cr.show + 1 === cr.ingredients.length) cr.show = 0
        else cr.show++
      }
    }
  }

</script>

<style>

</style>


