<template>
  <div id="container">
    <search></search>
    <quantity></quantity>
    <shop></shop>
  </div>
</template>

<script>

  import search from './Search'
  import quantity from './Quantity'
  import shop from './Shop'

  export default {

    components: { search, quantity, shop },
    
  }
</script>

<style>

/* RESET */

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

*, *:after, *:before {
  box-sizing: border-box;
}

/* PRINCIPAL */

html {
  font-family: 'PT Sans', sans-serif;
  font-size: 14px;
  color:#475965;
}

body {
  background:#f0f3f6;
  padding:30px 0;
}

.stronger {
  font-weight: 700;
}

#container {
  display:flex;
  justify-content: space-around;
  align-items: flex-start;
}

#search, #quantity, #shop {
  width:400px;
}

section {
  box-shadow: 1px 0px 20px rgba(0, 0, 0, 0.05);
}

.section-head {
  background:#53c4b8;
  padding:10px 10px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.section-head h2 {
  font-size:1.5rem;
  line-height:1.5rem;
  color:white;
}

.section-body {
  background:white;
  padding:15px 15px;
}

#search input {
  width:100%;
  height: 20px;
  line-height:20px;
  margin-bottom:15px;
}

.no-result, .results {
  height:20px;
  padding-top:2px;
}

.results {
  margin-bottom:20px;
  display:flex;
  justify-content: space-between;
  align-items: center;
}

.results i {
  transition: transform 0.2s ease-in;
}

.results i:hover {
  transform: rotate(-0.5turn);
  transition: transform 0.2s ease-in;
}

.result-craft {
  background:#f0f3f6;
  display:flex;
  justify-content:space-between;
  margin-top:5px;
  padding:5px 10px;
  border-radius:5px;
}

#search .result-craft:hover {
  background: #53c4b97c;
}

.result-info {
  display:flex;
  align-items: center;
}

.remove-craft {
  display:flex;
  align-items: center;
  justify-content: center;
  height:100%;
  width:25px;
}

.remove-craft i {
  font-size:1.3rem;
  margin-right:5px;
}
.remove-craft i:hover {
  color: #c45353f1;
}

.result-info img {
  width:35px;
  height:35px;
  margin-right:10px;
}

.result-name {
  display: flex;
  flex-flow: column;
}

.result-name span {
  font-size:0.90rem;
}

.result-right {
  display: flex;
  align-items: center;
}

.result-add {
  display:flex;
  align-items: center;
  justify-content: center;
  height:25px;
}

.result-add i {
  width:15px;
  height:15px;
  margin-left:10px;
}

.result-add .turn-i {
  transform: rotate(0turn);
}

.result-add .turn-i-act {
  transform:rotate(-0.25turn)
}

/* DECOMPOSITION */

#quantity .result-craft {
  background:#53c4b97c;
  border-radius:0;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

input[type="number"] {
  font-weight:700;
  font-size:0.9rem;
  background: #eafffd59;
  margin-left:20px;
  width:50px;
  height:30px;
  border:none;
  border-radius:2px;
  outline:none;
  text-align: center;
  padding-left:15px;
}

.decomposition {
  padding-left:10px;
}

ul {
  border-left: #53c4b94f 1px solid;
}

.result-crafts {
  display:flex;
  flex-flow: column;
}

.decomposition .result-craft {
  background:transparent!important;
  flex-flow: column;
  padding:0;
}

.decomposition .result-info {
  background: #f0f3f6;
  justify-content: space-between;
  border-radius:5px;
}

.decomposition .result-name {
  flex-flow: row;
  align-items: center;
}

.decomposition .true-craft {
  background:#53c4b959;
  padding: 3px 5px 2px 0;
}

.decomposition .result-info img {
  width:28px;
  height:28px;
  margin-right:5px;
  margin-left:5px;
}

.decomposition ul {
  padding-left:10px;
}

.decomposition .first-ul {
  padding-left:0;
}


</style>


