<template>
  <section id="shop">

    <div class="section-head">
      <h2>Courses</h2>
    </div>

    <div class="section-body">


      <div class="no-result" v-if="crafts.list.length === 0">Aucun craft dans la liste...</div>
      <div class="results" v-else>
        <span><span class="stronger">{{ getQt }}</span> ressources nécessaires : </span>
      </div>

      <div class="result-craft" :key="c.id" v-for="c in getShop">
      <div class="result-info">
        <img :src="'img/items/' + c.img + '.png'">
        <div class="result-name">
          {{ c.name }} x{{ c.qt }}<br>
          <span>{{ crafts.rarity.find( x => x.id === c.rarity ).name}}</span>
        </div>
      </div>
    </div>

    </div>


    <!-- <ul>
      <li :key="i.id" v-for="i in getShop">
        {{i.name}} x{{i.qt}}
      </li>
    </ul> -->
  </section>
</template>

<script>

  import crafts from './Store'

  export default {
    data() {
      return {
        crafts: crafts.state
      }
    },
    
    computed: {
      getShop () {
        let arr = []
        this.crafts.list.forEach((craft) => {
          crafts.getIngs(craft, arr)
        })

        return arr
      },

      getQt () {
        var allQt = 0
        this.getShop.forEach( (el) => {
          allQt += el.qt
        })
        return allQt
      }
    }
  }

</script>

<style scoped>

</style>


